/*================================================================
[Define variables]
==================================================================*/
/*==================================================================*/
:root {
  --xe_primary_color: #ef2c2c;
  --xe_seconary_color: #1e73be;
  --xe_base_text_color: #585858;
  --xe_base_heading_color: #4A4A4A;
  --xe_base_link_color: #0366E0;
  --xe_body_background_color: #ffffff;
  --xe_border_color: #eeeeee;
  --xe_inputs_border_color: #dedede;
  --xe_button_text_color: white;
  --xe_button_background_color: #ef2c2c;
  --xe_button_hover_text_color: #ffffff;
  --xe_button_hover_background_color: #1e73be;
  --xe_button_is_link_style_text_color: #ef2c2c;
  --xe_button_is_link_style_hover_text_color: #1e73be;
  --xe_button_is_link_style_underline_color: #ef2c2c;
  --xe_button_is_link_style_hover_underline_color: #1e73be;
  --xe_page_header_breadcrumb_background_color: #EEEEEE;
  --xe_woo_on_sale_text_color: #ffffff;
  --xe_woo_on_sale_background_color: red;
  --xe_woo_current_price_text_color: red;
  --xe_woo_previous_price_text_color: #3a2e2e;
  --xe_system_font_family: -apple-system, BlinkMacSystemFont, Helvetica, Arial, Segoe ui, System ui, sans-serif;
  --xe_text_emphasis_font_family: "Playfair Display", Georgia, Garamond, Baskerville, Baskerville Old Face, Hoefler Text, Times New Roman, Serif;
  --xe_monospace_font_family: monospace, monospace;
  --xe_base_font_size: 0.9375rem;
  --xe_base_font_weight: normal;
  --xe_base_letter_spacing: 0;
  --xe_base_font_line_height: 1.6;
  --xe_base_font_family: "Inter", sans-serif;
  --xe_heading_font_weight: bold;
  --xe_heading_letter_spacing: 0;
  --xe_heading_line_height: 1.2;
  --xe_heading_font_family: "Inter", sans-serif;
  --xe_button_font_size: 0.9375rem;
  --xe_button_font_weight: normal;
  --xe_button_line_height: 1;
  --xe_woo_product_title_size: 0.9375rem;
  --xe_buttons_border_radius: 3px;
  --xe_inputs_border_radius: 4px;
  --xe_grid_gutter_width: 30px;
  --xe_grid_gutter_width_none: 0;
  --xe_container_width: 1400px;
  --xe_container_width_narrow: 800px;
  --xe_container_width_wide: 100%;
  --xe_large_device_breakpoint: 1024px;
  --xe_medium_device_breakpoint: 991px;
  --xe_tablet_device_breakpoint: 768px;
  --xe_mobile_device_breakpoint: 575px;
  --xe_mobile_device_gutter_width: 5px;
  --xe_narrow_section_base_width: 700px;
  --xe_default_primary_area_width: 100%;
  --xe_default_sidebar_width: 25%;
  --xe_default_boxed_layout_maxwidth: 1170px;
  --xe_no_sidebar_narrow_container_width: 800px;
  --xe_default_boxed_layout_container_maxwidth: 1170px;
  --xe_default_layout_container_stretch_width: 100%;
  --xe_default_layout_container_stretch_maxwidth: 1920px;
  --xe_mobile_menu_breakpoint: 991px;
  --xe_block_standard_spacing: 60px;
  --xe_woo_product_item_spacing: 30px;
  --xe_iconfont: "themify";
  --xe_mobile_device_woo_product_title_font_size: 14px;
  --xe_site_navigation_zindex: 10000000;
  --xe_submenu_dropdown_zindex: 10000001;
}

/* -------------------------------------------------------------------------- 
	#. Import WooCommerce specific styles
   -------------------------------------------------------------------------- */
:root {
  --xe_woocommerce_price: #585858;
  --xe_woocommerce_sale_price: #FF0000;
  --xe_woocommerce_onsale_color: #FFFFFF;
  --xe_woocommerce_onsale_background_color: #FF0000;
  --xe_woocommerce_in_stock_color: #11A738;
  --xe_woocommerce_out_stock_color: #FF0000;
  --xe_woocommerce_alert_message: #00AD00;
  --xe_woocommerce_alert_info: #4A8EFF;
  --xe_woocommerce_alert_error: #FF0000;
  --xe_woocommerce_star_color: #FFD100;
  --xe_woocommerce_star_alternate_color: #D3CED2;
  --xe_woocommerce_quantity_input_width: 100px;
}

/* -------------------------------------------------------------------------- 
	#. Alert defaults
   -------------------------------------------------------------------------- */
.woocommerce-notices-wrapper {
  position: relative;
}

.woocommerce-error,
.woocommerce-info,
.woocommerce-message {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  border-top: 3px solid var(--xe_border_color);
  justify-content: space-between;
  padding: 15px 15px 15px 35px;
  margin-bottom: 30px;
  background-color: #F7F7F7;
}

.woocommerce-error::before,
.woocommerce-info::before,
.woocommerce-message::before {
  content: "\e64c";
  display: inline-flex;
  position: absolute;
  top: 15px;
  left: 13px;
  font-family: var(--xe_iconfont);
  font-size: 16px;
  font-weight: normal;
}

.woocommerce-error {
  border-top-color: var(--xe_woocommerce_alert_error);
}

.woocommerce-error::before {
  content: "\e717";
  color: var(--xe_woocommerce_alert_error);
}

.woocommerce-info {
  border-top-color: var(--xe_woocommerce_alert_info);
}

.woocommerce-info::before {
  content: "\e6b8";
  color: var(--xe_woocommerce_alert_info);
}

.woocommerce-message {
  border-top-color: var(--xe_woocommerce_alert_message);
}

.woocommerce-message::before {
  content: "\e64c";
  color: var(--xe_woocommerce_alert_message);
}

.woocommerce-info li,
.woocommerce-error li,
.woocommerce-message li {
  display: block;
  width: 100%;
  margin-bottom: 5px;
}

.woocommerce-info li:last-child,
.woocommerce-error li:last-child,
.woocommerce-message li:last-child {
  margin-bottom: 0;
}

.woocommerce-info li > strong,
.woocommerce-error li > strong,
.woocommerce-message li > strong {
  font-weight: 600;
}

.woocommerce .woocommerce-info .button,
.woocommerce .woocommerce-error .button,
.woocommerce .woocommerce-message .button,
.woocommerce-page .woocommerce-info .button,
.woocommerce-page .woocommerce-error .button,
.woocommerce-page .woocommerce-message .button {
  display: inline-flex;
  order: 2;
  float: none;
  position: relative;
  height: auto;
  margin: 0;
  padding: 0 0 2px 0;
  color: var(--xe_primary_color);
  background-color: transparent;
}

.woocommerce .woocommerce-info .button:hover,
.woocommerce .woocommerce-error .button:hover,
.woocommerce .woocommerce-message .button:hover,
.woocommerce-page .woocommerce-info .button:hover,
.woocommerce-page .woocommerce-error .button:hover,
.woocommerce-page .woocommerce-message .button:hover {
  color: var(--xe_seconary_color);
  background-color: transparent;
}

.woocommerce .woocommerce-info .button::after,
.woocommerce .woocommerce-info .button::before,
.woocommerce .woocommerce-error .button::after,
.woocommerce .woocommerce-error .button::before,
.woocommerce .woocommerce-message .button::after,
.woocommerce .woocommerce-message .button::before,
.woocommerce-page .woocommerce-info .button::after,
.woocommerce-page .woocommerce-info .button::before,
.woocommerce-page .woocommerce-error .button::after,
.woocommerce-page .woocommerce-error .button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: currentColor;
}

.woocommerce .woocommerce-info .button::before,
.woocommerce .woocommerce-error .button::before,
.woocommerce .woocommerce-message .button::before,
.woocommerce-page .woocommerce-info .button::before,
.woocommerce-page .woocommerce-error .button::before {
  content: "";
  z-index: 1;
  background-color: currentColor;
  transform-origin: right center;
  transform: scaleX(0);
  will-change: scale;
  transition: transform 0.26s cubic-bezier(0.25, 0.8, 0.25, 1);
  -webkit-backface-visibility: hidden;
}

.woocommerce .woocommerce-info .button::after,
.woocommerce .woocommerce-error .button::after,
.woocommerce .woocommerce-message .button::after,
.woocommerce-page .woocommerce-info .button::after,
.woocommerce-page .woocommerce-error .button::after,
.woocommerce-page .woocommerce-error .button::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  opacity: 0.5;
  background-color: currentColor;
}

.woocommerce .woocommerce-info .button:hover::before,
.woocommerce .woocommerce-error .button:hover::before,
.woocommerce .woocommerce-message .button:hover::before,
.woocommerce-page .woocommerce-info .button:hover::before,
.woocommerce-page .woocommerce-error .button:hover::before {
  transform: scale(1);
  transform-origin: left center;
  background-color: var(--xe_seconary_color);
}

.woocommerce a.button,
.woocommerce a.button.alt,
.woocommerce input.button,
.woocommerce button.button,
.woocommerce input.button.alt,
.woocommerce button.button.alt,
.woocommerce #respond input#submit,
.woocommerce #respond input#submit.alt,
.woocommerce ul.products li.product a.added_to_cart,
.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link {
  display: inline-flex;
  flex-direction: row;
  align-items: center;
  font-size: var(--xe_button_font_size);
  font-weight: normal;
  color: var(--xe_button_text_color);
  line-height: var(--xe_button_line_height);
  height: 48px;
  padding: 0 20px;
  background-color: var(--xe_button_background_color);
  border-radius: var(--xe_buttons_border_radius);
}
.woocommerce a.button:hover,
.woocommerce a.button.alt:hover,
.woocommerce input.button:hover,
.woocommerce button.button:hover,
.woocommerce input.button.alt:hover,
.woocommerce button.button.alt:hover,
.woocommerce #respond input#submit:hover,
.woocommerce #respond input#submit.alt:hover,
.woocommerce ul.products li.product a.added_to_cart:hover,
.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link:hover {
  color: var(--xe_button_hover_text_color);
  background-color: var(--xe_button_hover_background_color);
}
.woocommerce a.button:focus,
.woocommerce a.button.alt:focus,
.woocommerce input.button:focus,
.woocommerce button.button:focus,
.woocommerce input.button.alt:focus,
.woocommerce button.button.alt:focus,
.woocommerce #respond input#submit:focus,
.woocommerce #respond input#submit.alt:focus,
.woocommerce ul.products li.product a.added_to_cart:focus,
.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link:focus {
  outline-color: currentColor;
}

.woocommerce a.button.disabled,
.woocommerce a.button:disabled,
.woocommerce button.button.disabled,
.woocommerce button.button:disabled,
.woocommerce input.button.disabled,
.woocommerce input.button:disabled,
.woocommerce a.button:disabled[disabled],
.woocommerce #respond input#submit.disabled,
.woocommerce #respond input#submit:disabled,
.woocommerce button.button:disabled[disabled],
.woocommerce input.button:disabled[disabled],
.woocommerce #respond input#submit:disabled[disabled] {
  opacity: 1;
  padding: 0 20px;
  cursor: not-allowed;
  color: var(--xe_button_text_color);
  background-color: var(--xe_button_background_color);
}

.woocommerce #respond input#submit.alt.disabled,
.woocommerce #respond input#submit.alt:disabled,
.woocommerce #respond input#submit.alt:disabled[disabled],
.woocommerce a.button.alt.disabled,
.woocommerce a.button.alt:disabled,
.woocommerce a.button.alt:disabled[disabled],
.woocommerce button.button.alt.disabled,
.woocommerce button.button.alt:disabled,
.woocommerce button.button.alt:disabled[disabled],
.woocommerce input.button.alt.disabled,
.woocommerce input.button.alt:disabled,
.woocommerce input.button.alt:disabled:hover,
.woocommerce input.button.alt:disabled[disabled] {
  cursor: not-allowed;
  color: var(--xe_button_text_color);
  background-color: var(--xe_button_background_color);
}

.woocommerce a.button.alt.disabled:hover,
.woocommerce a.button.alt:disabled:hover,
.woocommerce input.button.alt.disabled:hover,
.woocommerce button.button.alt.disabled:hover,
.woocommerce button.button.alt:disabled:hover,
.woocommerce a.button.alt:disabled[disabled]:hover,
.woocommerce #respond input#submit.alt.disabled:hover,
.woocommerce #respond input#submit.alt:disabled:hover,
.woocommerce input.button.alt:disabled[disabled]:hover,
.woocommerce button.button.alt:disabled[disabled]:hover,
.woocommerce #respond input#submit.alt:disabled[disabled]:hover {
  color: var(--xe_button_hover_text_color);
  background-color: var(--xe_button_hover_background_color);
}

.woocommerce ul.products li.product .button.added {
  display: none;
}

.woocommerce ul.products li.product a.added_to_cart::before {
  content: "\e64c";
  font-family: var(--xe_iconfont);
  display: inline-flex;
  margin-right: 5px;
}

.xe-woo-buttons-group {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-direction: column;
  position: absolute;
  top: 50%;
  right: 15px;
  opacity: 1;
  visibility: visible;
  transform: translateY(-50%);
  transition: all 0.3s ease;
}
.xe-woo-buttons-group .xe-button {
  justify-content: center;
  width: 45px;
  height: 45px;
  font-size: 1rem;
  line-height: 47px;
  margin-bottom: 10px;
  color: var(--xe_base_text_color);
  background-color: white;
  border-radius: 100%;
}
.xe-woo-buttons-group .xe-button:hover {
  background-color: #e6e6e6;
}
.xe-woo-buttons-group .xe-add-to-cart-button::before {
  content: "";
  display: none;
}

@media (max-width: 575px) {
  .xe-woo-buttons-group .xe-button {
    width: 35px;
    height: 35px;
    font-size: 15px;
    line-height: 36px;
  }
}
.xe-add-to-cart-button {
  align-items: center;
}
.xe-add-to-cart-button::before {
  content: "\e60e";
  font-family: var(--xe_iconfont);
  margin-right: 5px;
}
.xe-add-to-cart-button::after {
  content: "";
  display: none;
}

.xe-add-to-cart-button.is-link-style {
  position: relative;
  padding: 0;
  text-decoration: none;
  color: var(--xe_button_is_link_style_text_color);
  background-color: transparent;
  border: none;
}
.xe-add-to-cart-button.is-link-style::before {
  content: "";
  display: none;
}
.xe-add-to-cart-button.is-link-style::after {
  content: "";
  display: inline-flex;
  position: absolute;
  bottom: -10px;
  left: 0;
  right: auto;
  width: 30px;
  height: 3px;
  background-color: var(--xe_button_is_link_style_underline_color);
  transition: all 0.8s ease;
}
.xe-add-to-cart-button.is-link-style:hover {
  text-decoration: none;
  color: var(--xe_button_is_link_style_hover_text_color);
  background-color: transparent;
  border: none;
}
.xe-add-to-cart-button.is-link-style:hover::after {
  content: "";
  right: 0;
  width: 100%;
  background-color: var(--xe_button_is_link_style_hover_underline_color);
}
.xe-add-to-cart-button.is-link-style:focus {
  box-shadow: none;
  border: none;
  background-color: transparent;
}

.woocommerce a.remove {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-size: 0;
  font-weight: normal;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
}

.woocommerce a.remove::before {
  content: "\e646";
  font-family: var(--xe_iconfont);
  font-weight: normal;
  font-size: 10px;
  line-height: 1;
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Tables
   -------------------------------------------------------------------------- */
.woocommerce table.shop_table,
.woocommerce table.shop_attributes {
  margin-bottom: 30px;
  border-top: 1px solid var(--xe_border_color);
  border-bottom: none;
}

.woocommerce table.shop_table th,
.woocommerce table.shop_table td,
.woocommerce table.shop_attributes th,
.woocommerce table.shop_attributes td {
  padding: 15px 10px;
  font-weight: normal;
  font-style: normal;
  font-size: var(--xe_base_font_size);
  line-height: var(--xe_base_font_line_height);
  border: none;
  border-top: none;
  border-bottom: 1px solid var(--xe_border_color);
  vertical-align: middle;
}

.woocommerce table.shop_table tbody th,
.woocommerce table.shop_table tfoot td,
.woocommerce table.shop_table tfoot th {
  border-top: none;
  font-weight: normal;
}

.woocommerce table.shop_attributes tr:nth-child(even) td,
.woocommerce table.shop_attributes tr:nth-child(even) th {
  background-color: transparent;
}

/* -------------------------------------------------------------------------- 
	#. Product loop
   -------------------------------------------------------------------------- */
.xe-woo-products-entry {
  display: block;
  position: relative;
  float: none;
  clear: both;
}

.xe-woo-products-entry::before,
.xe-woo-products-entry::after {
  content: " ";
  clear: both;
}

.woocommerce ul.products,
.woocommerce-page ul.products {
  position: relative;
  float: none;
  clear: both;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  flex-wrap: wrap;
  width: auto;
  max-width: inherit;
  margin: 0;
  padding: 0;
  list-style: none;
  margin-left: -15px;
  margin-right: -15px;
}

.woocommerce ul.products::before,
.woocommerce ul.products::after,
.woocommerce .products ul::after,
.woocommerce-page ul.products::after {
  content: "";
  display: flex;
  flex-wrap: wrap;
  clear: both;
  width: 100%;
}

.woocommerce ul.products li.product,
.woocommerce-page ul.products li.product {
  position: relative;
  display: inline-flex;
  flex-wrap: wrap;
  flex: 0 0 25%;
  width: auto;
  float: none;
  margin: 0 0 var(--xe_woo_product_item_spacing) 0;
  padding: 0;
  clear: both;
  list-style: none;
  padding-left: 15px;
  padding-right: 15px;
  overflow: hidden;
}

.woocommerce ul.products.columns-1 li.product,
.woocommerce-page ul.products.columns-1 li.product {
  flex: 0 0 100%;
}

.woocommerce ul.products.columns-2 li.product,
.woocommerce-page ul.products.columns-2 li.product {
  flex: 0 0 50%;
}

.woocommerce ul.products.columns-3 li.product,
.woocommerce-page ul.products.columns-3 li.product {
  flex: 0 0 33.33%;
}

.woocommerce ul.products.columns-4 li.product,
.woocommerce-page ul.products.columns-4 li.product {
  flex: 0 0 25%;
}

.woocommerce ul.products.columns-5 li.product,
.woocommerce-page ul.products.columns-5 li.product {
  flex: 0 0 20%;
}

.woocommerce ul.products.columns-6 li.product,
.woocommerce-page ul.products.columns-6 li.product {
  flex: 0 0 16.66%;
}

@media (max-width: 575px) {
  .woocommerce ul.products[class*=columns-].mobile-columns-1 li.product,
.woocommerce-page ul.products[class*=columns-].mobile-columns-1 li.product {
    flex: 100%;
  }

  .woocommerce ul.products[class*=columns-].mobile-columns-2 li.product,
.woocommerce-page ul.products[class*=columns-].mobile-columns-2 li.product {
    flex: 50%;
  }

  .woocommerce ul.products[class*=columns-].mobile-columns-2 {
    margin-left: -5px;
    margin-right: -5px;
  }

  .woocommerce ul.products[class*=columns-].mobile-columns-2 li.product {
    padding-left: -5px;
    padding-right: -5px;
  }
}
/* -------------------------------------------------------------------------- 
	#. Product
   -------------------------------------------------------------------------- */
.woocommerce ul.products li.product .xe-product-thumb {
  display: inline-flex;
  position: relative;
}

.woocommerce .xe-carousel ul.products li.product {
  margin-bottom: 0;
}

.wc-block-grid__product .wc-block-grid__product-title,
.woocommerce ul.products li.product .xe-product-title .xe-title {
  font-size: var(--xe_woo_product_title_size);
  font-weight: var(--xe_base_font_weight);
  line-height: var(--xe_base_font_line_height);
  padding: 0;
  margin-top: 10px;
  margin-bottom: 10px;
}

.woocommerce ul.products li.product .xe-title .xe-link {
  color: var(--xe_base_text_color);
}

.woocommerce ul.products li.product .xe-title .xe-link:hover {
  color: var(--xe_seconary_color);
}

.woocommerce ul.products li.product a img {
  display: block;
  margin: 0;
  max-width: 100%;
}

.woocommerce ul.products li.product .xe-product-thumb .xe-thumb-link img {
  display: inline-flex;
  margin: 0;
  max-width: 100%;
  animation: xeFadeIn 0.5s 0s;
  transition: all 0.5s ease;
}

.woocommerce ul.products li.product:hover .xe-product-thumb .xe-thumb-link img {
  transform: scale(1.05);
}

.woocommerce ul.products li.product .xe-product-thumb .xe-thumb-link img:nth-child(2) {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  animation: xeFadeOut 0.5s 0s;
  opacity: 0;
  visibility: hidden;
}

.woocommerce ul.products li.product:hover .xe-thumb-link.has-secondary-thumb img:first-child {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
  animation: xeFadeOut 0.5s 0s;
  opacity: 0;
  visibility: hidden;
}

.woocommerce ul.products li.product:hover .xe-thumb-link.has-secondary-thumb img:nth-child(2) {
  position: relative;
  z-index: inherit;
  opacity: 1;
  animation: xeFadeIn 0.5s 0s;
  opacity: 1;
  visibility: visible;
}

.woocommerce ul.products li.product .xe-woo-buttons-group.visible-on-hover {
  opacity: 0;
  visibility: hidden;
}

.woocommerce ul.products li.product:hover .xe-woo-buttons-group.visible-on-hover {
  opacity: 1;
  visibility: visible;
}

.woocommerce ul.products li.product:hover .xe-product-thumb .xe-woo-buttons-group.visible-on-hover .xe-button-item:first-child {
  animation: xeButtonFadeDown 0.3s 0s;
}

.woocommerce ul.products li.product:hover .xe-product-thumb .xe-woo-buttons-group.visible-on-hover .xe-button-item:nth-child(2) {
  animation: xeButtonFadeDown 0.6s 0s;
}

.woocommerce ul.products li.product:hover .xe-product-thumb .xe-woo-buttons-group.visible-on-hover .xe-button-item:nth-child(3) {
  animation: xeButtonFadeDown 0.9s 0s;
}

