.woocommerce-account .u-columns.col2-set > [class*=col-] {
  width: 50%;
}

.woocommerce-account form.login,
.woocommerce-account form.register {
  padding: 30px;
  margin: 0 0 30px 0;
  max-width: 100%;
  border-radius: 5px;
  border: 1px solid var(--xe_border_color);
}

.woocommerce-account form.login p.lost_password {
  margin-top: 20px;
  margin-bottom: 0;
}

/* -------------------------------------------------------------------------- 
	#. Dashboard
   -------------------------------------------------------------------------- */
.woocommerce-MyAccount-navigation ul {
  padding: 30px;
  margin: 0;
  list-style: none;
  border: 1px solid var(--xe_border_color);
  border-radius: 5px;
}

.woocommerce-MyAccount-navigation ul li {
  display: block;
  margin-bottom: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid var(--xe_border_color);
}

.woocommerce-MyAccount-navigation ul li:last-child {
  margin-bottom: 0;
  padding-bottom: 0;
  border-bottom: none;
}

.woocommerce-account .xe-entry-content .woocommerce-MyAccount-navigation ul li > a {
  color: var(--xe_base_text_color);
}

.woocommerce-account .xe-entry-content .woocommerce-MyAccount-navigation ul li > a:hover {
  color: var(--xe_seconary_color);
}

.woocommerce-MyAccount-navigation ul li > a::before {
  content: "\e665";
  display: inline-block;
  margin-right: 5px;
  font-weight: normal;
  font-family: var(--xe_iconfont);
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--dashboard > a::before {
  content: "\e6a9";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--orders > a::before {
  content: "\e687";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--downloads > a::before {
  content: "\e632";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--edit-address > a::before {
  content: "\e61d";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--edit-account > a::before {
  content: "\e602";
}

.woocommerce-MyAccount-navigation ul li.woocommerce-MyAccount-navigation-link--customer-logout > a::before {
  content: "\e682";
}

.woocommerce-MyAccount-content .woocommerce-Address-title > h3 {
  margin: 0 0 10px 0;
}

.woocommerce-MyAccount-content form.edit-account fieldset {
  margin-bottom: 20px;
}

.woocommerce-MyAccount-content .shop_table .woocommerce-MyAccount-downloads-file {
  gap: 5px;
}

.woocommerce-MyAccount-content .shop_table .woocommerce-MyAccount-downloads-file::before {
  content: "\e632";
  display: inline-block;
  font-family: var(--xe_iconfont);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Product Listing
   -------------------------------------------------------------------------- */
.wc-block-grid__products {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin: 0 -15px;
}

.wc-block-grid__product.wc-block-grid__product {
  position: relative;
  border: none;
  margin-bottom: 30px;
  padding-left: 15px;
  padding-right: 15px;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link {
  display: block;
  font-size: var(--xe_base_font_size);
  color: var(--xe_base_text_color);
  text-decoration: none;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link:hover {
  color: var(--xe_seconary_color);
  text-decoration: none;
}

.wc-block-grid__product.wc-block-grid__product .wc-block-grid__product-link:focus {
  outline: none;
}

.wc-block-grid__product .wc-block-grid__product-add-to-cart {
  margin-top: 10px;
  margin-bottom: 0;
}

.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link {
  position: relative;
}

.wc-block-grid__product-add-to-cart.wp-block-button .wp-block-button__link:hover {
  text-decoration: none;
}

.wc-block-grid__product .wc-block-grid__product-onsale {
  border: none;
  z-index: 10;
}

.wc-block-grid__product .wc-block-grid__product-rating {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 0 20px 0;
}

.wc-block-grid__product-rating .star-rating:before,
.wc-block-grid__product-rating .wc-block-grid__product-rating__stars:before {
  opacity: 1;
}

.wc-block-grid__product-rating .star-rating span:before,
.wc-block-grid__product-rating .wc-block-grid__product-rating__stars span:before {
  color: var(--xe_woocommerce_star_color);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Reviews
   -------------------------------------------------------------------------- */
.wc-block-components-review-list .wc-block-components-review-list-item__item {
  margin-bottom: 30px;
  padding-bottom: 10px;
  border-bottom: 1px solid var(--xe_border_color);
}

.wc-block-components-review-list .wc-block-components-review-list-item__item:last-child {
  padding-bottom: 0;
  border-bottom: 0;
}

.wc-block-components-review-list .wc-block-review-list-item__product {
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
  line-height: var(--xe_base_font_line_height);
  margin-right: 10px;
}

.wc-block-components-review-list .wc-block-review-list-item__product > a {
  color: var(--xe_base_text_color);
}

.wc-block-components-review-list .wc-block-review-list-item__product > a:hover {
  color: var(--xe_seconary_color);
}

.wc-block-components-review-list-item__meta {
  font-size: var(--xe_base_font_size);
  font-size: calc(var(--xe_base_font_size) - 2px);
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Price Filter
   -------------------------------------------------------------------------- */
.wp-block-woocommerce-price-filter > h3 {
  font-size: var(--xe_base_font_size);
  font-weight: var(--xe_base_font_weight);
  margin: 20px 0 30px 0;
}

.wc-block-components-price-slider__range-input-wrapper {
  margin: 0 0 40px 0;
}

.wc-block-components-price-slider__range-input-wrapper,
.wc-block-components-price-slider__range-input-progress {
  height: 4px;
}

.wc-block-components-price-slider__range-input {
  top: -4px;
}

/* -------------------------------------------------------------------------- 
	#. WooCommerce Block: Product Categories
   -------------------------------------------------------------------------- */
.wc-block-product-categories {
  margin-bottom: 30px;
}

.wc-block-product-categories-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.wc-block-product-categories-list li {
  display: block;
  margin: 10px 0;
}

.wc-block-product-categories-list li a {
  display: inline-flex;
  flex-wrap: wrap;
}

.wc-block-product-categories-list ul {
  margin: 0 0 0 20px;
}